.contact-container {
  width: 100%;
  height: 100vh;
  background-color: rgb(238, 85, 85);
}

.contact-content-holder {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  padding-left: 15%;
  gap: 30px;
}

.contact-title {
  margin-top: 20px;
  padding-bottom: 20;
  color: black;
  font-family: "Andada Pro", serif;
  font-weight: 400;
  font-size: 50px;
}

.email-el {
  font-size: 1.5rem;
  font-weight: 300;
  text-decoration: underline;
  text-decoration-thickness: 1.2px;
  color: black;
}

.socials {
  padding-top: 10px;
  font-size: 2rem;
  display: flex;
  gap: 40px;
}

.link-el {
  color: black;
  transition: 0.15s ease-in-out;
}

.link-el:hover {
  color: rgb(236, 236, 236);
}

@media screen and (max-width: 450px) {
  .contact-title {
    font-size: 40px;
  }

  .email-el {
    font-size: 1.2rem;
    font-weight: 300;
    text-decoration: underline;
    text-decoration-thickness: 1px;
    color: black;
  }

  .socials {
    font-size: 1.5rem;
    gap: 30px;
  }
}
